* {
  margin: 0;
  padding: 0; }

html, body {
  width: 100%;
  height: 100%; }

.row:before, .row:after {
  content: "";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px; }

.col-1 {
  width: 8.33333%; }

.col-2 {
  width: 16.66667%; }

.col-3 {
  width: 25%; }

.col-4 {
  width: 33.33333%; }

.col-5 {
  width: 41.66667%; }

.col-6 {
  width: 50%; }

.col-7 {
  width: 58.33333%; }

.col-8 {
  width: 66.66667%; }

.col-9 {
  width: 75%; }

.col-10 {
  width: 83.33333%; }

.col-11 {
  width: 91.66667%; }

.col-12 {
  width: 100%; }

header, footer {
  height: 5vh; }

nav, #mainPart, #alerts {
  height: 78vh; }

.border {
  border: 1px solid black; }

.grid-display {
  width: 1300px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

body {
  background: lightgray; }

#mainPart h2 {
  margin-top: 25vh;
  text-align: center;
  color: navy;
  font-size: 5em;
  font-family: 'Titan One', cursive;
  font-style: italic;
  text-shadow: 0 1px 0 #0040ff, 0 2px 0 #0080ff, 0 3px 0 #00bfff, 0 4px 0 #00ffff; }

#mainPart h3 {
  font-size: 1.5em;
  text-align: center;
  font-style: italic;
  font-family: 'Fredoka One', cursive;
  text-shadow: 0 1px 0 darkgray, 0 2px 0 gray, 0 3px 0 lightgray; }
